  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.swiper-cube .swiper-cube-shadow {
  position: absolute;
  left: 0;
  bottom: 0px;
  width: 100%;
  height: 100%;
  opacity: 0.6;
  z-index: 0;
}

.swiper-cube .swiper-cube-shadow:before {
  content: "";
  background: #000;
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  filter: blur(50px);
}

.swiper-flip {
  overflow: visible;
}

.swiper-flip .swiper-slide {
  pointer-events: none;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  z-index: 1;
}

.swiper-flip .swiper-slide .swiper-slide {
  pointer-events: none;
}

.swiper-flip .swiper-slide-active,
.swiper-flip .swiper-slide-active .swiper-slide-active {
  pointer-events: auto;
}

.swiper-flip .swiper-slide-shadow-top,
.swiper-flip .swiper-slide-shadow-bottom,
.swiper-flip .swiper-slide-shadow-left,
.swiper-flip .swiper-slide-shadow-right {
  z-index: 0;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.swiper-creative .swiper-slide {
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  overflow: hidden;
  transition-property: transform, opacity, height;
}

.swiper-cards {
  overflow: visible;
}

.swiper-cards .swiper-slide {
  transform-origin: center bottom;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  overflow: hidden;
}

@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Manrope";
  font-display: swap;
  src: url("./../fonts/manrope-medium.woff2") format("woff2"), url("./../fonts/manrope-medium.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Manrope";
  font-display: swap;
  src: url("./../fonts/manrope-semi-bold.woff2") format("woff2"), url("./../fonts/manrope-semi-bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Sans Caption";
  font-display: swap;
  src: url("./../fonts/pt-sans-caption-regular.woff2") format("woff2"), url("./../fonts/pt-sans -caption-regular.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-family: "Manrope", "Arial", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 24px;
  color: #181818;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

h1 {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

a {
  color: #000000;
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 15px;
}

.projects__wrapper {
  margin: 15px auto;
  padding: 80px 40px;
  border-radius: 20px;
  background-color: #f5f5f5;
}

.projects__top-content {
  display: grid;
  grid-template-columns: 32% 66%;
  gap: 20px;
  margin: 0 0 15px 0;
  min-height: 168px;
}

.projects__top-content h2 {
  order: 1;
  margin: -3px 0 0 2px;
  font-family: "PT Sans Caption", "Arial", sans-serif;
  font-weight: 400;
  font-size: 58px;
  line-height: 64px;
  letter-spacing: -1.7px;
  transform: translateX(150%);
  animation: animation-to-left 1s forwards;
}

.projects__top-content p {
  margin: 0;
  max-width: 261px;
  font-size: 14px;
  line-height: 24px;
  color: #787878;
  transform: translateX(-150%);
  animation: animation-to-right 1s forwards;
}

.projects__swiper-wrapper {
  position: relative;
  transform: translateY(150%);
  animation: animation-to-top 1.5s forwards;
}

.projects__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.projects__item {
  display: flex;
  flex-direction: column;
  gap: 10px;
  height: auto;
  border: 7px solid #ffffff;
  border-radius: 20px;
  background-color: #ffffff;
}
.projects__item:hover, .projects__item:focus-within {
  border: 7px solid #9cc91c;
  outline: none;
}

.projects__item-image {
  aspect-ratio: 419/294;
  border-radius: 15px;
  background-color: #e7e7e7;
  overflow: hidden;
}

.projects__item-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 15px;
  transition: transform 0.3s;
}

.projects__item:hover img,
.projects__item:focus-within img {
  transform: scale(1.1);
}

.projects__item-content {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 10px;
}

.projects__item-content h3 {
  margin: 0;
  font-weight: 600;
  font-size: 20px;
  line-height: 27px;
}

.projects__item-content p {
  flex-grow: 1;
  margin: 0;
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  color: #787878;
}

.projects__buttons-block {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 15px;
  margin: 15px 0 0 0;
}

.projects__item-button {
  position: relative;
  padding: 20px 35px 20px 25px;
  border-radius: 15px;
  font-size: 14px;
  line-height: 19px;
  user-select: none;
}
.projects__item-button:hover {
  padding: 22px 35px 18px 25px;
}

.projects__item-button--primary {
  color: #ffffff;
  background-color: #9cc91c;
}
.projects__item-button--primary:hover, .projects__item-button--primary:focus {
  background-color: #ffcf24;
  outline: none;
}
.projects__item-button--primary:active {
  opacity: 80%;
}

.projects__item-button--secondary {
  color: #787878;
  background-color: #f5f5f4;
}
.projects__item-button--secondary:hover, .projects__item-button--secondary:focus {
  color: #ffcf24;
  outline: none;
}
.projects__item-button--secondary:active {
  opacity: 80%;
}

.projects__item-button--secondary:hover span,
.projects__item-button--secondary:focus span {
  border-bottom: 2px solid #ffcf24;
}

.projects__item-button svg {
  position: absolute;
  top: 50%;
  right: 25px;
  transform: translateY(-50%);
}

.projects__item-button:hover svg {
  margin: 2px 0 0 0;
}

.projects__swiper-button {
  position: absolute;
  top: 52.1%;
  width: 57px;
  height: 56px;
  border: 9px solid #f5f5f5;
  border-radius: 50%;
  background-color: #ffffff;
  cursor: pointer;
  z-index: 10;
}
.projects__swiper-button:hover, .projects__swiper-button:focus {
  color: #ffcf24;
  outline: none;
}
.projects__swiper-button:disabled {
  background-color: #e7e7e7;
}

.projects__swiper-button--prev {
  left: -32px;
}

.projects__swiper-button--next {
  right: -32px;
}

@keyframes animation-to-right {
  0% {
    transform: translateX(-150%);
  }
  100% {
    transform: translateY(0);
  }
}
@keyframes animation-to-left {
  0% {
    transform: translateX(150%);
  }
  100% {
    transform: translateY(0);
  }
}
@keyframes animation-to-top {
  0% {
    transform: translateY(150%);
  }
  100% {
    transform: translateY(0);
  }
}